@import url(https://fonts.googleapis.com/css2?family=STIX+Two+Text:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe-list{
    border-right: 1px solid black;
    min-height:100vh;
    width: 50%;
}

.recipe-list__add-recipe-btn-container{
    padding-top: 30px;
    padding-bottom: 30px;
    text-align: center; 
}


.btn{
    background:none; 
    outline:none;
    border: none;
    font-size: inherit; 
    font-weight: 700;
    border-radius: 5px;
    padding: 3px 15px;
    cursor: pointer;
}

.btn--primary{
    color: white;
    background: hsl(200, 100%, 50%);
}

.btn--primary:hover{
    background: hsl(200, 100%, 35%);
}
.btn--danger{
    color: white;
    background: hsl(0, 100%, 50%);
}

.btn--danger:hover{
    background: hsl(0, 100%, 35%);
}

.recipe {
    padding: 10px;
    padding-bottom: 40px;
    border-bottom: 1px solid black;
}

.recipe:last-child {
    border-bottom: none;
    padding-bottom: 10px;
}

.recipe__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.recipe__title {
    margin: 0;
    font-size: 1.7rem;
    font-weight: 700;
}

.recipe__row {
    margin-top: 5px;
}

.recipe__label {
    margin-right: 4px;
    font-weight: 600;
}

.recipe__value{
    margin-top: 5px;
}
.recipe__value--indented {
    margin-right: 10px;
}

.recipe__instructions {
    white-space: pre-wrap;
}
.ingredient-grid{
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-row-gap: 4px;
    row-gap: 4px;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
    justify-content: flex-start
    ;
}
.recipe-edit{
    max-height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    width: 50%;
    padding: 10px;
    overflow-y: auto;
}

.recipe-edit__remove-button-cont{
    text-align: end;
}

.recipe-edit__remove-button{
    padding: 0;
    background: none;
    outline:none;
    border: none;
    font-size: 1rem;
    font-weight: 700;
}

.recipe-edit__details-grid{
    display: grid;
    grid-template-columns: auto 1fr;
    grid-row-gap: 10px;
    row-gap: 10px;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;   
}

.recipe-edit__label{
    font-weight: 600;
}

.recipe-edit__input{
    border: 1px solid black;
    border-radius: 5px;
    font-size: inherit;
    padding: 5px;
    outline:none;
    width: 100%;
    max-width: 25rem;
}

textarea.recipe-edit__input{
    resize: none;
    height: 150px;
}

.recipe-edit__ingredient-grid{
    display: grid;
    grid-template-columns: repeat(2, auto) 40px;
    grid-gap: 10px;
    gap: 10px;
    margin-top: 10px; 
    margin-left: 20px;
}

.recipe-edit__add-ingredient-button-cont{
    margin-top: 20px;
    text-align: center;
}

@media(max-width: 1000px){
    .recipe-edit__details-grid,
    .recipe-edit__ingredient-grid{
        grid-template-columns: 1fr;
    }
}
*, *::before, *::after{
    font-family: 'STIX Two Text', serif;
    box-sizing: border-box;
}

html{
    font-size: 1rem;
    font-weight: 400;
}

body{
    padding: 0;
    margin:0;
}


.mr-1{
    margin-right: 5px;
}
